import service from "@/utils/request";

// 2022/5/22 16:37
export function workerLogin(vo){
    return service({
        url:'/api/worker/login',
        method:'post',
        data:vo,
    })
}

// 2022/5/23 22:59
export function workerAdd(vo){
    return service({
        url:'/api/worker',
        method:'put',
        data:vo,
    })
}

// 2022/5/23 22:59
export function workerAll(){
    return service({
        url:'/api/worker/all',
        method:'get',
       
    })
}
//
export function DeleteOneWorker(id) { 
    return service({
        url: '/api/worker/'+id,
        method: 'delete',
        
    })
}
//
//
export function ModifyStaff(data) { //id
    return service({
        url: '/api/worker/modify',
        method: 'post',
		data:data
    })
}